import { Injectable } from '@angular/core';

import {  AngularFirestore } from 'angularfire2/firestore';

import { BLE } from '@ionic-native/ble/ngx';
import { BluetoothSerial } from '@ionic-native/bluetooth-serial/ngx';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { Tab2Page } from './tab2/tab2.page';
//import { setTimeout } from 'timers';


@Injectable({
  providedIn: 'root'
})
export class ServicioFirebaseService {
 
    
    Collection;

    constructor(private fireStore: AngularFirestore, private ble: BLE, private btle: BluetoothSerial, private geolocation: Geolocation) {
        this.Collection = fireStore.collection<any>('data');
        console.log(this.Collection);
        //this.ble =BLE;  
        var options = {
        enableHighAccuracy: true, timeout: 5000, maximumAge: 0
    };
    }
    

    suscribirse() {
        this.btle.subscribe('\n').subscribe(res => {
            this.add(JSON.stringify(res));
        }, err => { throw(err)});
    }

    //funci�n comprobante que se llama a s� misma cada medio segundo y se suscribe si el bt es� activo y el dispositivo est� conectado
    conectarse(id: string) {
        this.ble.autoConnect(id, () => { this.suscribirse() }, () => { })
    }

    //scan devices comprueba si el bt est� activado, si lo est� comprueba si est� conectado, si lo est� se suscribe, si no lo est� muestra
    //las opciones de bt y llama a la funci�n comprobante conectarse
    encenderBT() {
       this.ble.isEnabled().then(() => {
           console.log("bluetooth is enabled all G");
          
        }, () => {
            console.log("bluetooth is not enabled trying to enable it");
            this.ble.enable().then(() => {
                
            })
        });
       
    }
  
    add(data: string) {
        var now = new Date().getTime().toString();
        var lat = "";
        var long = "";
        var alt = "";
        this.geolocation.watchPosition().subscribe(posi => {
            lat = posi.coords.latitude.toString();
            long = posi.coords.longitude.toString();
           // alt = posi.coords.altitude.toString();
         }, err => {
        //   Tab2Page.prototype.presentToast("aaaaaaaaaaaaa");
        // Other info you want to add here
    })
            this.Collection.add({
                location: 'Latitud: ' + lat + ', Longitud: ' +long + 'Altitud: ' + alt,
                date: now,
                measurement: data,
                // Other info you want to add here
            })
            
     
        
    }
}
